.output-description{
  overflow-y: auto;
  color: grey;
  border-radius: 10px;   
}

.backdrop{
  position: fixed;
  top: 25%;
  bottom: 20%;
  left: 35%;
  right: 35%;
  padding: 5px;
  border-radius: 10px;
  background-color: white;
  border: 0.1vh solid black;
  height: fit-content;    
  opacity: 0.9;
}

::-webkit-scrollbar {
  width: 5px;
}

::-webkit-scrollbar-track {  
  box-shadow: inset 0 0 5px grey; 
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  background: rgb(255, 128, 192);
  border-radius: 10px;  
}

::-webkit-scrollbar-thumb:hover {
  background: pink; 
}


/* 1281px to higher resolution desktops*/
@media (min-width: 1281px) {      

  .backdrop{
    position: fixed;
    top: 25%;
    bottom: 20%;
    left: 35%;
    right: 35%;
    padding: 5px;
    border-radius: 10px;
    background-color: white;
    border: 0.1vh solid black;
    height: fit-content;    
    opacity: 0.9;
  }
  
     .output-description{
      overflow-y: auto;
      background-color: transparent;
      border: 0.1vh solid hotpink;
      opacity: 0.9;
      height: 30vh; 
      font-family: 'Muli'; 
    }  
    .title-popup{
      padding: 1.5vw;
      font-family: 'Muli';
      font-weight: bold;
      color: hotpink;
      font-size: 1.6rem;
    }
    .nutri-eval{
      color: black; 
      text-align: justify;
      font-family: 'Muli';
      font-size: 1.4rem;
      margin-left: 0.5vw;     
    }
}
  
